.header_icon-main {
  width: 24px;
  height: 24px;
  fill: none;
  stroke: #fff;
  visibility: visible;
  margin-top: 40px;
  margin-left: 20px;
}

.header_icon-main:hover, .header_icon-main:focus {
  stroke: #ff6b08;
  transition: all .4s;
}

