  text-transform: uppercase;
  text-align: right;
  font-family: Roboto;
  font-size: 12px;
}

.header_links:hover, .header_links:focus {
  color: #ff6b08;
  transition: all .4s;
}

.header_current-link:after {
  content: "";
  width: 36px;
  height: 3px;
  cursor: default;
  background-color: #ff001b;
  border-radius: 2px;
  display: block;
  position: absolute;
  top: 60px;
}

.header_main-link {
  visibility: hidden;
}

.header_button {
  width: 20px;
  height: 20px;
