  position: relative;
}

.header_button_icon {
  width: 12px;
  height: 12px;
  fill: none;
  stroke: #fff;
  left: 20px;
}

.header_button_icon:hover, .header_button_icon:focus {
  stroke: #ff6b08;
  transition: all .4s;
}

.header_form {
  width: 280px;
  height: 20px;
  margin: auto;
  position: relative;
}

.header_input {
  color: #fff;
  width: 280px;
  background-color: #0000;
  border: none;
  border-bottom: 1px solid #fff;
  margin-top: 60px;
  position: absolute;
}

.header_input:hover, .header_input:focus {
  color: #ff6b08;
  border-bottom: 1px solid #ff6b08;
  outline: none;
  transition: all .4s;
}

.header_error {
  text-align: center;
  color: #ff001b;
  visibility: hidden;
  padding-top: 10px;
  font-family: Roboto;
  font-size: 14px;
}

.header_img {
  height: 230px;
